'use client';

import { useEffect, useState } from 'react';
import { m, useMotionValue, useSpring, useTransform } from 'framer-motion';
import { useTranslation } from '@/components/providers/language-provider';

const modules = ['overview', 'explainer', 'autodm', 'iceball'] as const;
type ModuleKey = (typeof modules)[number];

const moduleSelectors: Record<ModuleKey, string[]> = {
  overview: ['a[href="/dashboard"]'],
  explainer: ['a[href="/explainer"]'],
  autodm: ['a[href="/autodm"]'],
  iceball: ['a[href="/iceball"]'],
};

const moduleGlow: Record<ModuleKey, string> = {
  overview: 'from-cyan-300/40 via-fuchsia-300/25 to-transparent',
  explainer: 'from-sky-300/35 via-emerald-300/25 to-transparent',
  autodm: 'from-orange-300/35 via-purple-300/25 to-transparent',
  iceball: 'from-blue-200/40 via-cyan-200/35 to-transparent',
};

const moduleOffsets: Record<ModuleKey, { x: number; y: number }> = {
  overview: { x: 0, y: 0 },
  explainer: { x: -25, y: -5 },
  autodm: { x: 20, y: 5 },
  iceball: { x: 10, y: -15 },
};

export function CoreSprite() {
  const [focus, setFocus] = useState<ModuleKey>('overview');
  const pointerX = useMotionValue(0);
  const pointerY = useMotionValue(0);
  const springX = useSpring(pointerX, { stiffness: 110, damping: 18 });
  const springY = useSpring(pointerY, { stiffness: 110, damping: 18 });
  const pupilX = useTransform(springX, (value) => value * 0.35);
  const pupilY = useTransform(springY, (value) => value * 0.35);
  const { t } = useTranslation();
  const [isBlinking, setIsBlinking] = useState(false);

  useEffect(() => {
    const onMove = (event: MouseEvent) => {
      const x = (event.clientX / window.innerWidth) * 2 - 1;
      const y = (event.clientY / window.innerHeight) * 2 - 1;
      pointerX.set(x * 30);
      pointerY.set(y * 20);
    };
    window.addEventListener('pointermove', onMove);
    return () => window.removeEventListener('pointermove', onMove);
  }, [pointerX, pointerY]);

  useEffect(() => {
    const unsubscribes: Array<() => void> = [];
    modules.forEach((key) => {
      moduleSelectors[key].forEach((selector) => {
        document.querySelectorAll(selector).forEach((element) => {
          const enter = () => {
            setFocus(key);
            const offset = moduleOffsets[key];
            pointerX.set(offset.x);
            pointerY.set(offset.y);
          };
          const leave = () => {
            setFocus('overview');
            pointerX.set(0);
            pointerY.set(0);
          };
          element.addEventListener('mouseenter', enter);
          element.addEventListener('mouseleave', leave);
          unsubscribes.push(() => {
            element.removeEventListener('mouseenter', enter);
            element.removeEventListener('mouseleave', leave);
          });
        });
      });
    });
    return () => unsubscribes.forEach((unsub) => unsub());
  }, [pointerX, pointerY]);

  useEffect(() => {
    let active = true;
    let blinkTimeout: ReturnType<typeof setTimeout>;
    const scheduleBlink = () => {
      const delay = 2500 + Math.random() * 4000;
      blinkTimeout = setTimeout(() => {
        if (!active) return;
        setIsBlinking(true);
        setTimeout(() => setIsBlinking(false), 140);
        scheduleBlink();
      }, delay);
    };
    scheduleBlink();
    return () => {
      active = false;
      clearTimeout(blinkTimeout);
    };
  }, []);

  const glow = moduleGlow[focus];

  const eyeVariants = {
    open: { scaleY: 1 },
    closed: { scaleY: 0.2 },
  };

  return (
    <div className="pointer-events-none relative flex items-center justify-center">
      <m.div
        animate={{ scale: [1, 1.08, 1], opacity: [0.4, 0.9, 0.4] }}
        transition={{ duration: 5, repeat: Infinity, ease: 'easeInOut' }}
        className={`absolute h-96 w-96 rounded-full blur-3xl bg-gradient-to-br ${glow}`}
      />
      <m.div
        className="pointer-events-auto flex h-80 w-80 items-center justify-center"
        animate={{ scale: [1, 1.02, 1], y: [-3, 3, -3] }}
        transition={{ duration: 7, repeat: Infinity, ease: 'easeInOut' }}
      >
        <div className="relative h-80 w-80 rounded-full bg-gradient-to-br from-purple-200/40 via-blue-200/25 to-cyan-200/30 backdrop-blur-3xl">
          <div className="absolute inset-4 rounded-full border border-white/25" />
          <div className="absolute inset-[14%] rounded-full bg-white/15 blur-3xl mix-blend-screen" />
          <div className="absolute inset-[24%] rounded-full border border-white/15" />
          <div className="absolute inset-[34%] rounded-full border border-white/10 opacity-70" />
          <div className="absolute left-1/2 top-[30%] h-16 w-44 -translate-x-1/2 rounded-full bg-white/30 blur-3xl" />
          <div className="absolute left-1/2 top-1/2 h-20 w-52 -translate-x-1/2 -translate-y-1/2 rounded-full bg-white/25 blur-xl" />
          <div className="absolute inset-0 flex items-center justify-center">
            <div className="h-40 w-40 rounded-full bg-white/20 blur-2xl" />
          </div>
          <div className="absolute inset-0 flex items-center justify-center gap-16">
            {['left', 'right'].map((side) => (
              <div key={side} className="relative flex h-16 w-16 items-center justify-center rounded-full">
                <m.div
                  className="absolute h-full w-full rounded-full bg-gradient-to-b from-white to-white/20 shadow-[0_0_18px_rgba(255,255,255,0.8)]"
                  variants={eyeVariants}
                  animate={isBlinking ? 'closed' : 'open'}
                  transition={{ duration: 0.2 }}
                />
                <m.div
                  style={{ x: pupilX, y: pupilY }}
                  className="relative h-8 w-8 rounded-full bg-[#131835] shadow-[0_0_14px_rgba(13,19,40,0.6)]"
                >
                  <div className="absolute inset-0 rounded-full bg-gradient-to-b from-cyan-400 to-purple-500 opacity-60" />
                  <div className="absolute left-1/2 top-1/2 h-3 w-3 -translate-x-1/2 -translate-y-1/2 rounded-full bg-white" />
                </m.div>
              </div>
            ))}
          </div>
        </div>
      </m.div>
      <span className="sr-only">{t('hero.sprite.label')}</span>
    </div>
  );
}
